@import "./reset.scss";
@import url('https://fonts.googleapis.com/css?family=Oswald');

html {
  font-family:'Oswald';
  box-sizing: content-box;
  background-color:black;
  height: 100%;
  overflow-x: hidden;
}

body{
  background-repeat:no-repeat;
  background-position: top;
  background-size:cover;
  overflow-x: hidden;
}


.mainPage{
  &.active{
    height:100vh;
    overflow:hidden;
  }
}

.content{
  overflow-x: hidden; 
  transition: transform .5s;
  &.active{
    transform:translateX(70%);
  }
}

#dynbackground{
  z-index:-1;
  position:absolute;
  top:0;
  left:0;
  width:100vw;
  &.portrait{
    width:inherit;
    height:100vh;
  }
}

.nav{
  color:white;
  font-size:2.5em;
  height:10%;
  display:flex;
  align-items:center;
  justify-content: center;
  .navbar{
    padding:25px;
    display:flex;
    justify-content: flex-end;
    align-items:center;
    height:50px;
    width:100%;
    a{
      transition:all .25s;
      color:white;
      text-decoration: none;
      &:hover{
        color:yellow;
        border-color:yellow;
      }
    }
    .nav__item{
      padding:10px;
      text-transform: uppercase;
    }
    .nav__item:hover{
      margin-left:25px;
      margin-right:25px;
    }
    .nav__item:not(:last-child){
      &:after{
        margin-left:30px;
        margin-right:10px;
        content:"Â·"
      }
    }
    .main{
      padding:5px;
      border:solid 2px white;
    }
    .left{
      margin-right:auto;
    }
  }
}
.page{
  height:100vh;
}

.sidebar{
  z-index:2;
  height:100%;
  width:70vw;
  background-color:white;
  position:absolute;
  left:-70vw;
  top:0%;
  transition:all .5s;
  display:flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  .sidebar__logo{
    margin-top:25px;
    margin-bottom:25px;
    margin-left:auto;
    margin-right:auto;
    width:30%;
    height:auto;
  }
  a.side__item{
    text-decoration: none;
    padding:10px;
    margin:15px;
    margin-top:5px;
    margin-bottom:5px;
    color:black;
    font-size:3em;
    text-transform: uppercase;
    width:75%;
    border:solid 4px black;
    &:first-child{
      margin-top:15px;
    }
  }
  &.active{
    transform: translateX(100%);
  }
}

.albumCover{
  display:flex;
  align-items: center;
  justify-content: center;
  transition:all 0.1s;
  opacity: 0.7;
  transform:scale(0.9);
  &:hover{
    opacity: 1;
    transform: scale(1);  
  }
}

.release-showcase{
  margin:100px;
  display:flex;
  align-items:center;
  justify-content: space-around;
  flex-wrap:wrap;
  img{
    margin-top:25px;
    margin-bottom:25px;
    height:350px;
    width:350px;
  }
}

.hamburger{
  height:40px;
  width:40px;
  display:none;
}

.overlay {
  display: none;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1;
  transition:all .5s;
  &.active {
    display: block;
  }
}

@media only screen and (max-width: 1100px) {
  .nav{
    font-size:1.5em;
  }
  .release-showcase{
    img{
      width:200px;
      height:auto;
    }
  }
}


@media only screen and (max-width: 600px){
  .nav__item{
    display:none;
  }
  .hamburger{
    display:block;
  }
}